/* body, html, * {
  font-size: 16px;
} */
.mat-button-wrapper {
  z-index: 0 !important;
}

.mat-mdc-dialog-surface, .mdc-dialog__surface {
  padding: 0 !important;
}

.cont-with-paginator {
  max-height: calc(100% - 72px) !important;
}

.mobile-label {
  display: none;
}

table {
  /* tr:nth-child(odd):not(tr.mat-header-row) {
      background-color: white !important;
  } */
}
table tr:nth-child(even) {
  background-color: rgb(250, 250, 250) !important;
}
table tr:nth-child(odd) {
  background-color: white !important;
}

body.dark table tr:nth-child(even) {
  background-color: #1e293b !important;
}
body.dark table tr:nth-child(odd):not(tr.mat-header-row) {
  background-color: #233146 !important;
}

.jeds-backdrop-overlay {
  background-color: rgba(0, 0, 0, 0.6);
}

.jeds-data-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.carousel-item {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

@media screen and (max-width: 960px) {
  table {
    border-radius: 4px;
  }
  table .mobile-label {
    width: 90px;
    display: block;
    font-weight: 600;
    text-align: left !important;
    float: left;
    color: rgb(112, 112, 112);
  }
  table td.mat-cell {
    width: 100%;
    text-align: right !important;
    padding: 1px !important;
    display: inline-block;
    border: none !important;
    flex: 1;
    align-items: center;
    overflow: hidden;
    min-height: inherit;
  }
  table td:before {
    content: attr(data-label);
    float: left;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 0.85em;
  }
  table tr.mat-header-row {
    display: none;
  }
  table tr.mat-row {
    display: flex;
    border-width: 0;
    border-bottom-width: 1px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.12);
    box-sizing: border-box;
    flex-direction: column;
    align-items: center;
    height: auto;
    padding: 9px 12px;
  }
  table tr:hover {
    background: none;
  }
  table td.mat-footer-cell {
    display: block;
    border: 0 none;
  }
  table td.mat-footer-cell.m-card-sub-title {
    margin-top: -8px;
    padding: 0 48px 0 16px;
    color: rgba(0, 0, 0, 0.54);
  }
  table tr.mat-footer-row {
    height: auto !important;
    display: grid;
    overflow: hidden;
    height: auto;
    position: relative;
    clear: both;
    background-color: #fff;
    padding: 12px;
    border: none;
  }
  table .alinearDerechaFooter {
    width: 100%;
    text-align: right;
  }
}
body.light mat-option:nth-child(even) {
  background-color: rgb(243, 243, 243);
}
body.light mat-option:nth-child(even):hover {
  background-color: rgb(213, 213, 213) !important;
}

body.light mat-option:nth-child(odd):hover {
  background-color: rgb(220, 220, 220) !important;
}

.filtroJeds {
  max-height: 100vh !important;
  overflow-y: auto !important;
}

.mat-chip[mat-sm-chip] {
  padding: 4px 6px 4px 6px !important;
  border-radius: 4px !important;
  font-size: 12px !important;
  line-height: 12px !important;
  max-height: 20px;
  box-sizing: border-box;
}

.mat-chip {
  position: relative;
  overflow: hidden;
}

.mat-chip:not(.mat-basic-chip) {
  display: inline-block;
  padding: 8px 12px 8px 12px;
  border-radius: 24px;
  font-size: 13px;
  line-height: 16px;
}